import $ from 'jquery'

$( document ).ready( function() {
	// handle double click
	$( '.esh-dblclick[ondblclick][onclick]:not([data-dblclick])' ).each(function( index, element ) {
		$( element ).attr( 'onclick',
			'event.preventDefault( );' +
			'event.stopImmediatePropagation( );' +
			'clearTimeout( event.target.clickTimeout );' +
			'event.target.clickTimeout = setTimeout( function( event ) {' +
				$( element ).attr( 'onclick' ) +
			'}, 300, event );')
		.attr( 'data-dblclick', '1' )
		.dblclick( event, function( e ) {
			clearTimeout( e.target.clickTimeout )
		} )
	} )
} )



